@charset "UTF-8";

@import url("reset.css");

@import url("font-awesome.css");

@import url("font.css");

@import url("../js/source/jquery.fancybox.css?v=2.1.5");

@import url("swiper.css");

/* CSS Document */
@media screen and (max-width: 667px) {
  * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #000;
  font-family: "open_sansregular", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: normal;
  outline: none;
  line-height: normal;
  background: url(../images/bg.png) no-repeat right, #0f1547;
  background: url(../images/bg.png) no-repeat right, -webkit-gradient(left top, right top, color-stop(0%, #0f1547), color-stop(100%, #3063c9));
  background: url(../images/bg.png) no-repeat right, -webkit-linear-gradient(left, #0f1547 0%, #3063c9 100%);
  background: url(../images/bg.png) no-repeat right, -moz-linear-gradient(left, #0f1547 0%, #3063c9 100%);
  background: url(../images/bg.png) no-repeat right, -ms-linear-gradient(left, #0f1547 0%, #3063c9 100%);
  background: url(../images/bg.png) no-repeat right, -o-linear-gradient(left, #0f1547 0%, #3063c9 100%);
  background: url(../images/bg.png) no-repeat right, linear-gradient(to right, #0f1547 0%, #3063c9 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0f1547', endColorstr='#3063c9', GradientType=1 );
}

.center {
  text-align: center;
}

p {
  margin: 0;
  padding: 0;
}

a, a:link, a:visited {
  color: #094cbc;
  text-decoration: none;
  outline: none;
}

a:hover, a:active {
  text-decoration: underline;
}

img {
  max-width: 100%;
  border: none;
}

.clear {
  height: 0;
  clear: both;
  visibility: hidden;
  display: block;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  width: 1170px;
}

@media screen and (max-width: 981px) {
  .container {
    width: 100%;
    padding: 0;
  }
}

@media screen and (min-width: 982px) {
  .container {
    width: 960px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

.header {
  width: 100%;
  float: left;
}

.header .logo {
  margin: 20px 0 15px 0;
  width: 242px;
  height: 60px;
  float: left;
}

.header .logo img {
  width: 242px;
  height: 60px;
}

.header .social {
  clear: right;
  margin: 10px 0 0 0;
  float: right;
  text-align: right;
}

.header .social a {
  margin: 0 0 0 10px;
}

.menu {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 47px;
  float: left;
  background: url(../images/bg_menu.gif);
  position: relative;
  z-index: 3;
}

.menu .container {
  height: 47px;
  background: url(../images/bg_overmenu.png) center no-repeat;
}

.menu li {
  display: inline-block;
  margin: 0;
  padding: 0;
  height: 46px;
  background: url(../images/bg_menu.png) right no-repeat;
  font-size: 14px;
  -webkit-transition: background 0.5s ease-in-out;
  -ms-transition: background 0.5s ease-in-out;
  transition: background 0.5s ease-in-out;
}

.menu li a {
  display: block;
  padding: 17px 30px 0 30px;
  height: 29px;
  display: block;
  color: #ffffff;
  text-decoration: none;
}

.menu li:hover {
  background: url(../images/bg_menuhover.gif) #f8f8f8;
}

.menu li a:hover, .menu li:hover a {
  color: #000;
  text-decoration: none;
}

.menu li .submenu {
  display: none;
  position: absolute;
  left: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  background: url(../images/bg_menuhover.gif) #f8f8f8;
  -webkit-box-shadow: 0 5px 5px 0 rgba(50, 50, 50, 0.45);
  -moz-box-shadow: 0 5px 5px 0 rgba(50, 50, 50, 0.45);
  box-shadow: 0 5px 5px 0 rgba(50, 50, 50, 0.45);
  z-index: 2;
  -webkit-transition: opacity 0.8s ease-in-out;
  -ms-transition: opacity 0.8s ease-in-out;
  transition: opacity 0.8s ease-in-out;
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  opacity: 0;
}

.menu li:hover .submenu {
  display: block;
  position: absolute;
  padding: 20px 0;
  width: 100%;
  filter: alpha(opacity = 100);
  -moz-opacity: 1;
  opacity: 1;
}

.menu li:hover .submenu .container {
  background: none;
}

.menu li:hover .submenu ul {
  margin: 0;
  padding: 0;
  width: 20%;
  float: left;
  background: url(../images/bg_submenu_line.gif) right no-repeat;
}

.menu li:hover .submenu ul li {
  margin: 0;
  padding: 0;
  background: none;
}

.menu li:hover .submenu ul li a {
  margin: 0;
  padding: 10px;
}

.menu li:hover .submenu ul li a:hover {
  color: #003DA0;
}

.menu li:hover .submenu ul li a img {
  border: none;
  width: 100%;
  max-width: 200px;
}

.menu li:hover .submenu ul li h2 {
  margin: 0 0 10px 0;
  padding: 0;
  font-size: 16px;
  font-weight: 400;
}

.mobilemenu, .menum, .menum li, .menum li .submenu {
  display: none;
}

@media screen and (max-width: 667px) {
  .header .social, .menu {
    display: none;
  }

  .header .logo {
    margin: 10px;
    width: 201px;
    height: 50px;
    float: left;
  }

  .header .logo img {
    width: 201px;
    height: 50px;
  }

  .mobilemenu {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    margin: 0 12px 0 0;
    width: 54px;
    height: 69px;
    float: right;
    background: url(../images/m_menu.png);
  }

  .menum {
    display: none;
    margin: 0;
    padding: 0;
    width: 100%;
    float: left;
    background: #222;
  }

  .menum li {
    display: block;
    margin: 0;
    padding: 0;
    width: 100%;
    font-size: 14px;
    border-bottom: #444 solid 1px;
  }

  .menum li a {
    display: block;
    padding: 10px 15px;
    color: #ffffff;
    text-decoration: none;
  }

  .menum li a:hover {
    color: #000;
    text-decoration: none;
  }

  .menum li:hover {
    background: #f8f8f8;
  }

  .menum li:hover a {
    color: #000;
    text-decoration: none;
  }

  .menum li .submenu {
    display: none;
    margin: 0;
    padding: 0;
    width: 100%;
    float: left;
    background: #ffffff;
    list-style: none;
    color: #000;
  }

  .menum li:hover .submenu {
    display: block;
  }

  .menum li .submenu li {
    margin: 0;
    padding: 1%;
    width: 48%;
    float: left;
    border: none;
  }

  .menum li .submenu li img {
    width: 100%;
  }

  .menum li .submenu li h2 {
    margin: 0 0 5px 0;
    padding: 0;
    font-size: 14px;
  }
}

@media screen and (max-width: 982px) {
  .menu li a {
    padding: 17px 18px 0 18px;
  }

  .header {
    margin: 0 2%;
    width: 96%;
  }

  .menu li:hover .submenu ul li h2 {
    font-size: 16px;
  }
}

.banner {
  width: 100%;
  float: left;
  overflow: hidden;
}

.productslist {
  padding: 60px 4% 45px 4%;
  width: 92%;
  float: left;
  background: #ffffff;
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(19%, #ffffff), color-stop(100%, #dfdfdf));
  background: -webkit-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -moz-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -ms-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -o-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#dfdfdf', GradientType=0 );
}

.productslist ul {
  margin: 0;
  padding: 0;
}

.productslist ul li {
  display: inline;
  margin: 0;
  padding: 0;
  width: 25%;
  float: left;
}

.productslist ul li .head {
  font-family: "SukhumvitSet", Helvetica, Arial, sans-serif;
  font-size: 34px;
}

.productslist ul li .subhead {
  font-family: "SukhumvitSet", Helvetica, Arial, sans-serif;
  font-size: 26px;
}

.productslist ul li.center {
  text-align: center;
  font-weight: bold;
  font-size: 16px;
}

.productslist ul li a {
  color: #000;
}

.productslist ul li a:hover {
  text-decoration: none;
  color: #003DA0;
}

.productslist ul li img {
  margin: 0 0 5px 0;
  width: 100%;
  max-width: 195px;
}

@media screen and (max-width: 667px) {
  .productslist ul li {
    margin: 0 0 15px 0;
    width: 100%;
    text-align: center;
  }

  .productslist ul li.center {
    padding: 0 1%;
    width: 50%;
  }

  .productslist {
    width: 100%;
  }

  .productslist ul li .head {
    font-size: 28px;
  }

  .productslist ul li .subhead {
    font-size: 24px;
  }
}

@media screen and (min-width: 668px) and (max-width: 982px) {
  .productslist {
    padding: 50px 2% 45px 2%;
    width: 96%;
  }

  .productslist ul li .head {
    font-size: 20px;
  }

  .productslist ul li .subhead {
    font-size: 16px;
  }

  .productslist ul li.center {
    padding: 0 1%;
    width: 23%;
    font-size: 14px;
  }
}

@media screen and (min-width: 983px) and (max-width: 1169px) {
  .productslist ul li .head {
    font-size: 26px;
  }

  .productslist ul li .subhead {
    font-size: 20px;
  }
}

.vdoheader {
  padding: 60px 3% 25px 3%;
  width: 94%;
  float: left;
  background: #0b2b9e;
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #0b2b9e), color-stop(100%, #092074));
  background: -webkit-linear-gradient(top, #0b2b9e 0%, #092074 100%);
  background: -moz-linear-gradient(top, #0b2b9e 0%, #092074 100%);
  background: -ms-linear-gradient(top, #0b2b9e 0%, #092074 100%);
  background: -o-linear-gradient(top, #0b2b9e 0%, #092074 100%);
  background: linear-gradient(to bottom, #0b2b9e 0%, #092074 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0b2b9e', endColorstr='#092074', GradientType=0 );
}

.vdoheader h2 {
  margin: 0;
  padding: 0;
  float: left;
  font-family: Helvetica, Arial, sans-serif;
  color: #ffffff;
  font-size: 36px;
  font-weight: normal;
}

.vdoheader .link {
  float: right;
  color: #ffffff;
  text-decoration: none;
}

.vdoheader .link img {
  margin: 15px 0 0 0;
}

.vdohilight {
  display: block;
  position: relative;
  padding: 0;
  width: 50%;
  float: left;
  background: #ffffff;
}

.vdohilight img {
  width: 100%;
  filter: alpha(opacity = 90);
  -moz-opacity: 0.90;
  opacity: 0.90;
  -webkit-transition: opacity .5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.vdohilight .wording {
  position: absolute;
  bottom: 3px;
  left: 0;
  padding: 15px 3%;
  width: 94%;
  background: url(../images/bg_vdohilight.png);
  font-size: 14px;
  color: #ffffff;
  z-index: 1;
  filter: alpha(opacity = 65);
  -moz-opacity: 0.65;
  opacity: 0.65;
  -webkit-transition: opacity .5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.vdohilight .wording h2 {
  margin: 0;
  padding: 0;
  font-family: "SukhumvitSet", Helvetica, Arial, sans-serif;
  color: #ffffff;
  font-size: 30px;
  font-weight: 600;
}

.vdohilight:hover .wording {
  filter: alpha(opacity = 100);
  -moz-opacity: 1;
  opacity: 1;
}

.vdohilight:hover img {
  filter: alpha(opacity = 100);
  -moz-opacity: 1;
  opacity: 1;
}

@media screen and (max-width: 667px) {
  .vdohilight, .vdoheader {
    width: 100%;
    clear: both;
  }

  .vdohilight .wording {
    width: 100%;
  }

  .vdoheader h2 {
    margin: 0 0 10px 0;
    font-size: 24px;
  }

  .vdoheader .link {
    margin: 0 0 20px 0;
    float: left;
    clear: left;
  }
}

@media screen and (max-width: 981px) {
  .vdohilight .wording h2 {
    font-size: 20px;
  }
}

@media screen and (min-width: 982px) {
  .vdohilight .wording h2 {
    font-size: 24px;
  }
}

.latestnews {
  margin: 0;
  padding: 60px 3%;
  width: 94%;
  float: left;
  background: #f4f4f4;
  list-style: none;
}

.latestnews h2 {
  margin: 0 0 20px 0;
  padding: 0;
  color: #2954af;
  font-size: 36px;
  font-weight: 400;
  text-align: left;
}

.latestnews .readmore {
  margin: -40px 0 0 0;
  text-align: right;
  float: right;
}

.latestnews li {
  margin: 0;
  padding: 0 1%;
  width: 31.33%;
  float: left;
}

.latestnews li img {
  margin: 0;
  border: none;
}

.latestnews li h2 {
  margin: 0 0 10px 0;
  padding: 0;
  font-size: 20px;
  font-weight: normal;
}

.latestnews li .desc {
  color: #333;
}

.latestnews li .content {
  padding: 4%;
  width: 92%;
  background: #ffffff;
}

@media screen and (max-width: 667px) {
  .latestnews h2 {
    margin: 0 0 10px 0;
    font-size: 24px;
  }

  .latestnews .readmore {
    margin: 0 0 20px 0;
    float: left;
    clear: left;
  }

  .latestnews li {
    width: 100%;
    clear: both;
    margin: 0 0 15px 0;
  }

  .latestnews li img {
    margin: 0 0 10px 0;
    max-width: 350px;
  }

  .latestnews li h2 {
    font-size: 18px;
  }

  .latestnews {
    width: 100%;
  }
}

@media screen and (max-width: 981px) {
  .latestnews li img {
    width: 100%;
  }

  .latestnews h2 {
    font-size: 30px;
  }
}

@media screen and (min-width: 982px) {
  .latestnews li img {
    width: 100%;
  }
}

.welcomeproducts {
  margin: 0;
  padding: 0;
  width: 100%;
  float: left;
  background: #ffffff;
}

.welcomeproducts li {
  position: relative;
  margin: 0 0 -4px 0;
  padding: 0;
  width: 33.334%;
  float: left;
}

.welcomeproducts li img {
  margin: 0;
  padding: 0;
  width: 100%;
  border: none;
}

.welcomeproducts li .desc {
  position: absolute;
  margin: 30% 0 0 0;
  width: 100%;
  color: #ffffff;
}

.welcomeproducts li .desc h2 {
  margin: 0 0 5px 0;
  padding: 0;
  font-size: 32px;
  font-weight: 400;
}

.welcomeproducts li .desc .link {
  margin: 0 auto;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.5s, opacity 0.5s linear;
  font-size: 0px;
  color: #ffffff;
}

.welcomeproducts li:hover .desc .link {
  display: block;
  margin: 0 auto;
  padding: 6px 0;
  width: 140px;
  background: #ffffff;
  border: #FFFFFF solid 1px;
  font-family: "SukhumvitSet", Helvetica, Arial, sans-serif;
  font-size: 18px;
  color: #000;
  text-align: center;
  text-decoration: none;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  -webkit-transition: color .5s ease-out, background .2s ease-in;
  -moz-transition: color .5s ease-out, background .2s ease-in;
  -ms-transition: color .5s ease-out, background .2s ease-in;
  -o-transition: color .5s ease-out, background .2s ease-in;
  transition: color .5s ease-out, background .2s ease-in;
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}

.welcomeproducts li:hover .desc .link:hover {
  background: #FFC600;
  text-decoration: none;
}

@media screen and (max-width: 667px) {
  .welcomeproducts li {
    width: 100%;
  }

  .welcomeproducts li .desc h2 {
    font-size: 28px;
  }

  .welcomeproducts li .desc .link {
    margin: 10px auto 0 auto;
    width: 100px;
    font-size: 0px;
  }

  .welcomeproducts li:hover .desc .link {
    margin: 10px auto 0 auto;
    width: 100px;
    font-size: 18px;
  }
}

@media screen and (max-width: 982px) {
  .welcomeproducts li .desc h2 {
    font-size: 26px;
  }
}

.presenter {
  margin: 0;
  padding: 0 0 30px 0;
  width: 100%;
  float: left;
  background: url(../images/graph-paper.png) #1941aa;
  list-style: none;
}

.presenter .header {
  margin: 0 0 20px 0;
  padding: 40px 2% 35px 2%;
  width: 96%;
  background: url(../images/graph-paper.png) #152e7e;
  font-size: 36px;
  font-weight: 300;
  color: #ffffff;
}

.presenter li {
  display: inline-block;
  position: relative;
  width: 16.66%;
  float: left;
  background: url(../images/bg_sponsor_line.png) no-repeat right;
  text-align: center;
  font-size: 14px;
  color: #ffffff;
}

.presenter li img {
  margin: 0 0 10px 0;
  border: none;
}

.presenter li.last {
  background: none;
}

.presenter li .number {
  position: absolute;
  left: 60%;
  top: 10px;
  font-size: 32px;
  font-weight: bold;
}

.presenter a {
  color: #ffffff;
  text-decoration: none;
}

@media screen and (max-width: 667px) {
  .presenter .header {
    font-size: 24px;
  }

  .presenter li {
    margin: 0 0 10px 0;
    width: 50%;
  }

  .presenter .header {
    width: 100%;
  }
}

/*-- Products page --*/
.productshead {
  padding: 15px 2%;
  width: 96%;
  float: left;
  font-family: "SukhumvitSet", Helvetica, Arial, sans-serif;
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #ffffff), color-stop(19%, #ffffff), color-stop(100%, #dfdfdf));
  background: -webkit-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -moz-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -ms-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: -o-linear-gradient(top, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #ffffff 19%, #dfdfdf 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#dfdfdf', GradientType=0 );
}

.productshead h1 {
  margin: 0;
  padding: 0;
  font-size: 34px;
  font-weight: bold;
}

.productshead .subhead {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

@media screen and (max-width: 667px) {
  .productshead {
    width: 100%;
  }
}

/*-- Products --*/
.container-products {
  margin: 0;
  padding: 1px 0 0 1px;
  width: 100%;
  float: left;
  background: #ffffff;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  list-style: none;
}

.container-products li {
  position: relative;
  margin: -1px 0 0 -1px;
  padding: 25px;
  width: 25.08%;
  height: 260px;
  float: left;
  border: solid 1px #e4e4e4;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: box-shadow 0.5s;
  -ms-transition: box-shadow 0.5s;
  transition: box-shadow 0.5s;
}

.container-products li:hover {
  -webkit-box-shadow: inset 0 0 10px 0 rgba(50, 50, 50, 0.35);
  -moz-box-shadow: inset 0 0 10px 0 rgba(50, 50, 50, 0.35);
  box-shadow: inset 0 0 10px 0 rgba(50, 50, 50, 0.35);
}

.container-products li .new {
  position: absolute;
  top: 13px;
  left: -4px;
  width: 52px;
  height: 27px;
  background: url(../images/icon_new.png);
}

.container-products li .sale {
  position: absolute;
  top: 13px;
  left: -4px;
  width: 76px;
  height: 27px;
  background: url(../images/icon_sale.png);
  color: #ffffff;
}

.container-products li .sale p {
  margin: 6px 0 0 12px;
  padding: 0;
  font-size: 12px;
}

.container-products .frame {
  padding: 10px 0 0 0;
  height: 100px;
  text-align: center;
}

.container-products img {
  margin: 0;
  padding: 0;
  border: none;
}

.container-products .line {
  margin: 10px 0;
  height: 1px;
  background: #e4e4e4;
}

.container-products .name {
  color: #000;
  line-height: 140%;
  font-size: 18px;
