/*-- Slider --*/
/* Browser Resets
*********************************/
.flex-container a:active, .flexslider a:active, .flex-container a:focus, .flexslider a:focus {
  outline: none;
}

.slides, .flex-control-nav, .flex-direction-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Icon Fonts
*********************************/
/* Font-face Icons */
@font-face {
  font-family: 'flexslider-icon';
  src: url("font/flexslider-icon.eot");
  src: url("font/flexslider-icon.eot?#iefix") format("embedded-opentype"), url("font/flexslider-icon.woff") format("woff"), url("font/flexslider-icon.ttf") format("truetype"), url("font/flexslider-icon.svg#flexslider-icon") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* FlexSlider Necessary Styles
*********************************/
.flexslider {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  float: left;
  clear: both;
}

.flexslider .slides > li {
  display: none;
  -webkit-backface-visibility: hidden;
}

/* Hide the slides before the JS is loaded. Avoids image jumping */
.flexslider .slides img {
  width: 100%;
  display: block;
}

.flex-pauseplay span {
  text-transform: capitalize;
}

/* Clearfix for the .slides element */
.slides:after {
  content: "\0020";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

html[xmlns] .slides {
  display: block;
}

* html .slides {
  height: 1%;
}

/* No JavaScript Fallback */
/* If you are not using another script, such as Modernizr, make sure you
 * include js that eliminates this class on page load */
.no-js .slides > li:first-child {
  display: block;
}

/* FlexSlider Default Theme
*********************************/
.flexslider {
  margin: 0;
  background: #ffffff;
  position: relative;
}

.flex-viewport {
  max-height: 2000px;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.loading .flex-viewport {
  max-height: 300px;
}

.flexslider .slides {
  zoom: 1;
}

.carousel li {
  margin-right: 5px;
}

/* Direction Nav */
.flex-direction-nav {
  *height: 0;
}

.flex-direction-nav a {
  display: block;
  position: absolute;
  top: 50%;
  z-index: 10;
  margin: -30px 0 0 0;
  width: 37px;
  height: 62px;
  overflow: hidden;
  opacity: 0;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.8);
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.3);
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  transition: all .3s ease;
}

.flex-direction-nav .flex-prev {
  left: -50px;
}

.flex-direction-nav .flex-next {
  right: -50px;
  text-align: right;
}

.flexslider:hover .flex-prev {
  opacity: 0.7;
  left: 0;
  background: url(../images/banner_left.gif);
}

.flexslider:hover .flex-next {
  opacity: 0.7;
  right: 0;
  background: url(../images/banner_right.gif);
}

.flexslider:hover .flex-next:hover, .flexslider:hover .flex-prev:hover {
  opacity: 1;
}

.flex-direction-nav .flex-disabled {
  opacity: 0 !important;
  filter: alpha(opacity = 0);
  cursor: default;
}

.flex-direction-nav a:before {
  display: block;
  font-size: 0;
  height: 60px;
  content: '\f001';
}

.flex-direction-nav a.flex-next:before {
  content: '\f002';
}

/* Pause/Play */
.flex-pauseplay a {
  display: block;
  position: absolute;
  bottom: 5px;
  left: 10px;
  width: 20px;
  height: 20px;
  opacity: 0.8;
  z-index: 10;
  overflow: hidden;
  cursor: pointer;
  color: #000;
}

.flex-pauseplay a:before {
  display: inline-block;
  font-family: "flexslider-icon";
  font-size: 20px;
  content: '\f004';
}

.flex-pauseplay a:hover {
  opacity: 1;
}

.flex-pauseplay a.flex-play:before {
  content: '\f003';
}

/* Control Nav */
.flex-control-nav {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
}

.flex-control-nav li {
  display: inline-block;
  zoom: 1;
  *display: inline;
  margin: 0 4px;
}

.flex-control-paging li a {
  display: block;
  width: 10px;
  height: 10px;
  background: #ffffff;
  background: rgba(225, 225, 225, 0.9);
  cursor: pointer;
  text-indent: -9999px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -o-border-radius: 20px;
  border-radius: 20px;
  -webkit-box-shadow: inset 0 0 1px rgba(225, 225, 225, 0.8);
  -moz-box-shadow: inset 0 0 1px rgba(225, 225, 225, 0.8);
  -o-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.8);
  box-shadow: inset 0 0 1px rgba(225, 225, 225, 0.8);
}

.flex-control-paging li a:hover {
  background: #000;
  background: rgba(19, 163, 230, 0.8);
}

.flex-control-paging li a.flex-active {
  background: #000;
  background: rgba(19, 163, 230, 1);
  cursor: default;
}

.flex-control-thumbs {
  position: static;
  margin: 5px 0 0;
  overflow: hidden;
}

.flex-control-thumbs li {
  margin: 0;
  width: 25%;
  float: left;
}

.flex-control-thumbs img {
  display: block;
  width: 100%;
  opacity: .7;
  cursor: pointer;
}

.flex-control-thumbs img:hover {
  opacity: 1;
}

.flex-control-thumbs .flex-active {
  opacity: 1;
  cursor: default;
}

@media screen and (max-width: 667px) {
  .flex-direction-nav .flex-prev {
    opacity: 1;
    left: 10px;
  }

  .flex-direction-nav .flex-next {
    opacity: 1;
    right: 10px;
  }
}

/*-- Animate --*/
.imgslideUp {
  animation-name: imgslideUp;
  -webkit-animation-name: imgslideUp;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  animation-timing-function: ease;
  -webkit-animation-timing-function: ease;
  visibility: visible !important;
}

@keyframes imgslideUp {
  0% {
    transform: translateY(50%);
    opacity: 0;
  }

  100% {
    transform: translateY(0%);
  }
}

@-webkit-keyframes imgslideUp {
  0% {
    -webkit-transform: translateY(50%);
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0%);
  }
}

.imgslide {
  visibility: hidden;
}

.playerpopup {
  background: #070a3d;
  color: #ffffff;
}
